import React from 'react'
import { v4 as uuidv4 } from 'uuid'
import {
  Card,
  FormText,
  CardHeader,
  CardTitle,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  TabContent,
  TabPane,
  Label
} from 'reactstrap'
import Swiper from 'react-id-swiper'
import 'swiper/css/swiper.css'
import options from './AssetOption'
import AssetAccordion from '../Portfolio/AssetAccordion'
import '../../assets/scss/plugins/extensions/swiper.scss'
import img1 from '../../assets/img/slider/banner-31.jpg'
import img2 from '../../assets/img/slider/banner-22.jpg'
import img3 from '../../assets/img/slider/banner-23.jpg'
import img4 from '../../assets/img/slider/banner-24.jpg'
import img5 from '../../assets/img/slider/banner-25.jpg'
import axios from 'axios'
import classnames from 'classnames'
import { connect } from 'react-redux'
import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import { ToastContainer } from 'react-toastify'
import '../../assets/scss/plugins/extensions/toastr.scss'
import '../../assets/scss/plugins/extensions/dropzone.scss'
import { getData, addData } from '../../redux/actions/data-list/'
import Select from 'react-select'
import { Edit, Trash, Delete } from 'react-feather'
import themeConfig from '../../configs/themeConfig'
import { encryptdata } from '../../utility/context/SecurityTool'
import '../../assets/scss/pages/data-list.scss'
import '../../assets/scss/components/app-loader.scss'
import Sidebar from '../Nominee/DataListSidebar'
const params = {
  effect: 'cube',
  grabCursor: true,
  autoplay: {
    delay: 2000,
    disableOnInteraction: true
  },
  cubeEffect: {
    shadow: true,
    slideShadows: true,
    shadowOffset: 20,
    shadowScale: 0.94
  },
  pagination: {
    el: '.swiper-pagination'
  }
}

const colourOptions2 = [
  { value: 'Text', label: 'Text', color: '#00B8D9', isFixed: true },
  { value: 'textarea', label: 'Text Area', color: '#0052CC', isFixed: true },
  { value: 'Date', label: 'Date', color: '#0052CC', isFixed: true },
  { value: 'Number', label: 'Number', color: '#0052CC', isFixed: true },
  { value: 'File', label: 'File', color: '#0052CC', isFixed: true }
]

class AddAssets extends React.Component {
  state = {
    messages: this.props.messages,
    open: false,
    deleteID: null,
    fname: '',
    ftype: '',
    assetList: [],
    liabilityList: [],
    assetShow: true,
    assetType: '',
    active: '1',
    sidebar: false,
    currentData: null,
    isLoading: false,
    selectAssetRef: React.createRef(),
    selectNomineeRef: React.createRef(),
    selectCustomRef: React.createRef(),
    nomineeOption: [
      {
        value: 'Add Nominee',
        label: 'Add Nominee',
        color: '#00B8D9',
        isFixed: false
      }
    ],
    nominees: [],
    nomineeList: [],
    modal: false,
    selectedTemplate: [],
    isAddDisabled: true,
    template: this.props.messages?.assetOption
      ? this.props.messages?.assetOption
      : options,
    colourOptions1: [
      {
        value: 'Bank Details',
        label: this.props.messages?.colorOption?.['Bank Details']
          ? this.props.messages?.colorOption?.['Bank Details']
          : 'Bank Details',
        color: '#00B8D9',
        isFixed: true
      },
      {
        value: 'Fixed Deposit',
        label: this.props.messages?.colorOption?.['Fixed Deposit']
          ? this.props.messages?.colorOption?.['Fixed Deposit']
          : 'Fixed Deposit',
        color: '#0052CC',
        isFixed: true
      },
      {
        value: 'Policy',
        label: this.props.messages?.colorOption1?.Policy
          ? this.props.messages?.colorOption1?.Policy
          : 'Policy',
        color: '#00B8D9',
        isFixed: true
      },
      {
        value: 'Property',
        label: this.props.messages?.colorOption?.['Property']
          ? this.props.messages?.colorOption?.['Property']
          : 'Property',
        color: '#00B8D9',
        isFixed: true
      },
      {
        value: 'Mutual Fund',
        label: this.props.messages?.colorOption?.['Mutual Fund']
          ? this.props.messages?.colorOption?.['Mutual Fund']
          : 'Mutual Fund',
        color: '#00B8D9',
        isFixed: true
      },
      {
        value: 'Provident Fund',
        label: this.props.messages?.colorOption?.['Provident Fund']
          ? this.props.messages?.colorOption?.['Provident Fund']
          : 'Provident Fund',
        color: '#00B8D9',
        isFixed: true
      },
      {
        value: 'Gratuity',
        label: this.props.messages?.colorOption?.['Gratuity']
          ? this.props.messages?.colorOption?.['Gratuity']
          : 'Gratuity',
        color: '#00B8D9',
        isFixed: true
      },
      {
        value: 'Receipt / Bill',
        label: this.props.messages?.colorOption?.['Receipt / Bill']
          ? this.props.messages?.colorOption?.['Receipt / Bill']
          : 'Receipt / Bill',
        color: '#00B8D9',
        isFixed: true
      },
      {
        value: 'Custom',
        label: this.props.messages?.colorOption?.['Custom']
          ? this.props.messages?.colorOption?.['Custom']
          : 'Custom',
        color: '#0052CC',
        isFixed: true
      }
    ]
  }

  deletecus = (e) => {
    const temp3 = this.state.selectedTemplate.filter((x) => {
      return e.key !== x.key
    })
    this.setState({ selectedTemplate: temp3 })
  }
  getAssets = () => {
    const user = JSON.parse(localStorage.getItem('logInUserData'))
    const { assetList, liabilityList } = this.state
    assetList.splice(0, assetList.length)
    liabilityList.splice(0, liabilityList.length)

    user?.assets?.length &&
      user.assets.forEach((e, i) => {
        if(e.id) {
        let nomin = ''
        e?.nominees?.length &&
          e.nominees.forEach((n, idx) => {
            if (idx === e.nominees.length - 1) {
              nomin = nomin + n.name
            } else {
              nomin = nomin + n.name + ', '
            }
          })
        const cont = (
          <div>
            <Row>
              {e?.assetDetails?.map((x, i) => {
                if (x.val !== '') {
                  return (
                    <Col md="5" sm="12" key={i + x.key}>
                      <FormText>
                        <b>{x.key} :</b> {x.val}
                      </FormText>
                    </Col>
                  )
                } else return ''
              })}
              <Col md="5" sm="12">
                <FormText>
                  <b>Nominees :</b> {nomin}
                </FormText>
              </Col>
              <Col md="2" sm="12">
                <FormText>
                  <b>
                    <Edit
                      className="cursor-pointer mr-1"
                      size={window.screen.width < 600 ? 13 : 20}
                      onClick={() => {
                        this.editItem(e)
                      }}
                    />
                  </b>
                  <b>
                    <Trash
                      className="cursor-pointer"
                      size={window.screen.width < 600 ? 13 : 20}
                      onClick={() => {
                        console.log('event', e)
                        this.setState({ deleteID: e.id, open: true })
                      }}
                    />
                  </b>
                </FormText>
              </Col>
            </Row>
          </div>
        )
        const ast = {
          id: i + 1,
          title: e.assetType,
          content: cont
        }
        assetList.push(ast)
      }
      })
    this.setState({ assetList: assetList?.reverse() })
  }
  static getDerivedStateFromProps(props, state) {
    if (props.dataList.data.length !== state.nomineeList.length) {
      const nomineeOption = state.nomineeOption
      props.dataList.data.forEach((e) => {
        const temp = {
          value: e._id,
          label: e.name + ' - ' + e.relation,
          color: '#00B8D9',
          isFixed: false
        }
        nomineeOption.push(temp)
      })

      return {
        nomineeList: props.dataList.data,
        nomineeOption: nomineeOption
      }
    }

    // Return null if the state hasn't changed
    return null
  }
  componentDidMount() {
    !this?.props?.dataList?.data?.length &&
      !this.props.listLoading &&
      this.props.getData()
    this.getAssets()
    // this.props.getInitialData()
  }

  toggleModal = () => {
    if (this.state.modal) {
      this.clearCustomField()
    }
    this.setState((prevState) => ({
      modal: !prevState.modal
    }))
  }

  changeValue = (e, k) => {
    const selectedTemplate = this.state.selectedTemplate
    const idx = selectedTemplate.findIndex((x) => x.key === k.key)
    if (e.length < 50) {
      selectedTemplate[idx].val = e
      this.setState({ selectedTemplate }, this.addAssetsButtonValidation)
    }
  }

  addAssetsButtonValidation = () => {
    let isAddDisabled = false
    this.state.selectedTemplate.forEach((f) => {
      if (f.mandatory && !f.val) {
        isAddDisabled = true
      }
    })
    this.setState({
      isAddDisabled: isAddDisabled
    })
  }

  savechanges = (e) => {

    e.preventDefault()
    const user = JSON.parse(localStorage.getItem('logInUserData'))
    const as = {
      assetType: this.state.assetType,
      assetDetails: this.state.selectedTemplate,
      nominees: this.state.nominees,
      id: this.state.editId

    }

    const idx = user.assets.findIndex((el) => el.id === this.state.editId)
    if (idx >= 0) {
      user.assets.splice(idx, 1)
      user.assets.push(as);
    } else return

    localStorage.setItem('logInUserData', JSON.stringify(user))
    
    axios
      .post(
        '/backendapi/adddetails',
        { _id: user._id, assets: encryptdata(JSON.stringify(user.assets)) },
        {
          headers: {
            Authorization: 'Bearer ' + localStorage.getItem('authtoken')
          }
        }
      )
      .then((res) => {
        toast.success('Edited successfully!')
        this.props.getData(this.props.parsedFilter)
        this.getAssets()
      })
      .catch()

    this.clearAsset()
    this.clearCustomField()
    this.setState({ isAddDisabled: true })

  }

  callAddAsset = (e) => {
    e.preventDefault()
    const user = JSON.parse(localStorage.getItem('logInUserData'))
    const as = {
      assetType: this.state.assetType,
      assetDetails: this.state.selectedTemplate,
      nominees: this.state.nominees,
      id: uuidv4()
    }
    user.assets.push(as)
    localStorage.setItem('logInUserData', JSON.stringify(user))
    toast.success('Asset addedd successfully!')
    axios
      .post(
        '/backendapi/adddetails',
        {
          _id: user._id,
          assets: encryptdata(JSON.stringify(user.assets)),
          nominees: this.state.nominees
        },
        {
          headers: {
            Authorization: 'Bearer ' + localStorage.getItem('authtoken')
          }
        }
      )
      .then((res) => {
        this.props.getData(this.props.parsedFilter)
        this.getAssets()
      })
      .catch()

    this.clearAsset()
    this.clearCustomField()
    this.setState({ isAddDisabled: true })
  }

  deleteAsset = (id) => {
    // e.preventDefault()
    const user = JSON.parse(localStorage.getItem('logInUserData'))
    // const as = {
    //   assetType: this.state.assetType,
    //   assetDetails: this.state.selectedTemplate,
    //   nominees: this.state.nominees
    // }

    const idx = user.assets.findIndex((el) => el.id === id)
    if (idx >= 0) {
      user.assets.splice(idx, 1)
    } else return

    localStorage.setItem('logInUserData', JSON.stringify(user))
    toast.warning('Deleted successfully!')
    axios
      .post(
        '/backendapi/adddetails',
        { _id: user._id, assets: encryptdata(JSON.stringify(user.assets)) },
        {
          headers: {
            Authorization: 'Bearer ' + localStorage.getItem('authtoken')
          }
        }
      )
      .then((res) => {
        this.props.getData(this.props.parsedFilter)
        this.getAssets()
      })
      .catch()

    this.clearAsset()
    this.clearCustomField()
    this.setState({ isAddDisabled: true })
  }

  addfield = () => {
    const selectedTemplate = this.state.selectedTemplate
    const size = this.state.selectedTemplate.length
    let temp = ''
    if (selectedTemplate[size - 1].key === 'Attachments') {
      temp =
        this.state.selectedTemplate.length &&
        selectedTemplate.splice(this.state.selectedTemplate?.length - 1, 0, {
          key: this.state.fname,
          val: '',
          type: this.state.ftype,
          deletable: true
        })
      this.setState({ temp, fname: '', ftype: '' })
    } else {
      selectedTemplate.push({
        key: this.state.fname,
        val: '',
        type: this.state.ftype,
        deletable: true
      })
      this.setState({ selectedTemplate, fname: '', ftype: '' })
    }
    this.toggleModal()
  }

  handleSidebar = (boolean, addNew = false) => {
    this.setState({ sidebar: boolean })
    if (addNew === true) this.setState({ currentData: null, addNew: true })
  }

  addData = async (obj) => {
    this.setState({ isLoading: true })
    try {
      await this.props.addData(obj)
      this.setState({ isLoading: false })
      this.handleSidebar(false, true)
    } catch {
      this.setState({ isLoading: false })
      toast.error('Failed to add Nominee! Please retry')
    }
  }
  changeNominee = (event) => {
    const nominees = []
    if (event && event[0] && event?.[0]?.value === 'Add Nominee') {
      this.setState({ sidebar: true })
    } else {
      event &&
        event[0] &&
        event.forEach((e) => {
          nominees.push({ id: e.value, name: e.label })
        })
      !event && this.setState({ isAddDisabled: true })
      this.setState({ nominees })
    }
  }

  clearAsset = () => {
    this.state.selectAssetRef.current.select.clearValue()
    this.state.selectNomineeRef.current.select.clearValue()
    this.setState({ assetType: '', selectedTemplate: [], nominees: [] })
  }
  clearCustomField = () => {
    this.setState({ fname: '', ftype: '' })
    this.state.selectCustomRef.current &&
      this.state.selectCustomRef.current.select.clearValue()
  }
  editItem = (item) => {
    const { assetDetails, assetType, nominees } = item // item.id is left
    this.setState({ assetType })
    this.setState({ editItem: true })
    this.setState({ editId: item.id })
    this.setState({ selectedTemplate: assetDetails })
    this.setState({ nominees })
  }
  animateLabel = (value) => {
    if (value) {
      document.getElementById('buttonLabel').classList.remove('no-display')
      document.getElementById('buttonLabel').classList.add('button-text')
    } else {
      document.getElementById('buttonLabel').classList.add('no-display')
      document.getElementById('buttonLabel').classList.remove('button-text')
    }
  }

  render() {
    const { messages, thumbView, getData, parsedFilter } = this.props
    const {
      fname,
      ftype,
      modal,
      addNew,
      active,
      sidebar,
      nominees,
      template,
      assetType,
      isLoading,
      currentData,
      isAddDisabled,
      editItem,
      nomineeOption,
      selectAssetRef,
      selectCustomRef,
      selectedTemplate,
      selectNomineeRef
    } = this.state
    return (
      <div className={`data-list ${thumbView ? 'thumb-view' : 'list-view'}`}>
        <Modal
          isOpen={this.state.open}
          className="modal-dialog-centered"
          backdrop="static"
        >
          <ModalBody>Are you sure you want to delete this record?</ModalBody>
          <ModalFooter>
            <Button
              color="primary"
              onClick={() => this.setState({ open: false })}
              className="button-label"
            >
              Cancel
            </Button>
            <Button
              color="danger"
              onClick={() => {
                this.deleteAsset(this.state.deleteID)
                this.setState({ open: false })
              }}
              className="button-label"
            >
              Delete
            </Button>
          </ModalFooter>
        </Modal>
        <Sidebar
          show={sidebar}
          data={currentData}
          updateData={this.updateData}
          addData={this.addData}
          isLoading={isLoading}
          handleSidebar={this.handleSidebar}
          thumbView={thumbView}
          getData={getData}
          dataParams={parsedFilter}
          addNew={addNew}
        />
        <div
          className={classnames('data-list-overlay', {
            show: sidebar
          })}
          onClick={() => this.handleSidebar(false, true)}
        />
        <Row>
          <Col lg="8" md="6" sm="12">
            <div style={{ width: window.screen.width < 500 ? '100%' : '150%' }}>
              <Row>
                <Col sm="12">
                  <TabContent activeTab={active}>
                    <TabPane tabId="1">
                      <Row>
                        <ToastContainer />
                        <Col lg="8" md="6" sm="12">
                          <Card>
                            <CardHeader>
                              <CardTitle>
                                {messages?.subHeading
                                  ? messages.subHeading
                                  : editItem ? 'Edit Asset Details' : 'Add New Asset'}
                              </CardTitle>
                            </CardHeader>
                            <CardBody>
                              <Form>
                                <Row>
                                  <Col md="6" sm="12">
                                    <FormGroup className="form-label-group">
                                    
                                      <Select
                                        className="React"
                                        classNamePrefix="select"
                                        ref={selectAssetRef}
                                        isDisabled={editItem}
                                        value={ editItem ? this.state?.colourOptions1.filter(option => 
                                          option.value === assetType) : null}
                                        name="color"
                                        options={this.state?.colourOptions1}
                                        isClearable={true}
                                        placeholder={
                                          messages?.assetOptionText
                                            ? messages.assetOptionText
                                            : 'Select Asset Type'
                                        }
                                        onChange={(e) => {
                                          this.setState({
                                            isAddDisabled: true,
                                            fname: '',
                                            ftype: '',
                                            assetType: e ? e.value : undefined,
                                            selectedTemplate: e
                                              ? template[e.value]
                                              : {}
                                          })
                                        }}
                                      />
                                      <Label for="nameMulti">
                                        {messages?.assetOptionText
                                          ? messages.assetOptionText
                                          : 'Select Asset Type'}
                                      </Label>
                                    </FormGroup>
                                  </Col>
                                  <Col md="6" sm="12">
                                    
                                    <FormGroup className="form-label-group">
                                      <Select
                                        isMulti
                                        name="nominee"
                                        options={nomineeOption}
                                        value={editItem ? nominees.map(n=> {
                                            return {value: n.id, label: n.name}
                                        }) : null}
                                        ref={selectNomineeRef}
                                        className="React"
                                        classNamePrefix="select"
                                        placeholder={
                                          messages?.nomineeOption
                                            ? messages.nomineeOption
                                            : 'Select Nominees'
                                        }
                                        id="nominee"
                                        onChange={(e) => {
                                          this.changeNominee(e)
                                        }}
                                        //onChange={(e) => {{this.setState({nominee: e ? e.value : undefined})}} }
                                      />
                                      <Label>
                                        {messages?.nomineeOption
                                          ? messages.nomineeOption
                                          : 'Select Nominees'}
                                      </Label>
                                    </FormGroup>
                                  </Col>
                                </Row>
                                <Row>
                                  {Object.keys(template).includes(assetType) &&
                                    selectedTemplate.map((e) => {
                                      return (
                                        <Col md="6" sm="12">
                                          <FormGroup className="form-label-group">
                                            {e.deletable && (
                                              <Delete
                                                style={{
                                                  position: 'absolute',
                                                  right: '-5px',
                                                  top: '-7px'
                                                }}
                                                className="primary"
                                                size={15}
                                                onClick={() => {
                                                  this.deletecus(e)
                                                }}
                                              />
                                            )}
                                            <Input
                                              type={e.type}
                                              name="city"
                                              id="cityMulti"
                                              className="input-label"
                                              placeholder={e.key}
                                              value={e.val}
                                              onChange={(ev) => {
                                                this.changeValue(
                                                  ev.target
                                                    ? ev.target.value
                                                    : ev.value,
                                                  e
                                                )
                                              }}
                                            />
                                            <Label
                                              for="cityMulti"
                                              className={
                                                themeConfig.theme === 'dark'
                                                  ? 'dark-label'
                                                  : 'light-label'
                                              }
                                            >
                                              {e.key}
                                            </Label>
                                          </FormGroup>
                                        </Col>
                                      )
                                    })}
                                </Row>
                                <Row style={{ marginTop: '15px' }}>
                                  <Col
                                    md="6"
                                    sm="12"
                                    style={{ display: 'flex' }}
                                  >
                                    {assetType && (
                                      <>
                                        <Button
                                          color="white"
                                          outline
                                          onClick={this.toggleModal}
                                          className="add-button"
                                          id="addButton"
                                          onMouseEnter={() =>
                                            this.animateLabel(true)
                                          }
                                          onMouseLeave={() =>
                                            this.animateLabel(false)
                                          }
                                          style={{
                                            backgroundColor: 'var(--warning)'
                                          }}
                                        >
                                          +
                                        </Button>
                                        <div className="label-div">
                                          <span
                                            id="buttonLabel"
                                            className="no-display"
                                          >
                                            Add a Custom Field
                                          </span>
                                        </div>
                                      </>
                                    )}
                                  </Col>
                                  <Col sm="6">
                                    <FormGroup
                                      style={{
                                        textAlign: 'right',
                                        marginBottom: '0px'
                                      }}
                                    >
                                      <Button.Ripple
                                        outline
                                        color="secondary"
                                        type="reset"
                                        className="button-label"
                                        onClick={(e) => {
                                          this.setState({ editItem: false })
                                          this.clearAsset()
                                        }}
                                      >
                                        {editItem
                                          ? 'Cancel'
                                          : messages?.resetButton
                                          ? messages?.resetButton
                                          : 'Reset'}
                                      </Button.Ripple>{' '}
                                      <Button.Ripple
                                        color="warning"
                                        disabled={
                                          nominees.length === 0 ||
                                          !assetType ||
                                          isAddDisabled
                                        }
                                        type="reset"
                                        className="button-label"
                                        onClick={(e) => {
                                          editItem
                                          ? this.savechanges(e) :
                                          this.callAddAsset(e)
                                        }}
                                      >
                                        {editItem
                                          ? 'Update'
                                          : messages?.submitButton
                                          ? messages?.submitButton
                                          : 'Add'}
                                      </Button.Ripple>
                                    </FormGroup>
                                  </Col>
                                </Row>
                              </Form>
                            </CardBody>
                          </Card>
                        </Col>
                      </Row>

                      <Row>
                        <ToastContainer />
                        <Col md="8">
                          <AssetAccordion
                            heading={
                              messages?.textList
                                ? messages?.textList
                                : 'Assets List'
                            }
                            collapseItems={this.state?.assetList}
                            colorOption={this.state?.messages?.colorOption}
                          />
                        </Col>
                      </Row>
                    </TabPane>
                  </TabContent>
                </Col>
              </Row>
            </div>
          </Col>
          <Col lg="4" md="6" sm="12">
            <div className="swipermobile">
              <Swiper {...params}>
                <div
                  style={{
                    backgroundImage: `url(${img1})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img2})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img3})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img4})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img5})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
              </Swiper>
            </div>
          </Col>
        </Row>

        <Row>
          {/* To add a */}
          <Col sm="12">
            <Modal
              isOpen={modal}
              toggle={this.toggleModal}
              className="modal-dialog-centered"
            >
              <ModalHeader toggle={this.toggleModal}>
                Add Custum Field
              </ModalHeader>
              <ModalBody>
                <FormGroup className="form-label-group">
                  <Input
                    type="email"
                    name="Email"
                    disabled={assetType === ''}
                    id="EmailMulti"
                    value={fname}
                    placeholder="Enter Field Name"
                    onChange={(e) => {
                      this.setState({ fname: e.target.value })
                    }}
                  />
                  <Label for="EmailMulti">Enter Field Name</Label>
                </FormGroup>

                <FormGroup
                  className="form-label-group"
                  disabled={assetType === undefined}
                >
                  <Select
                    className="React"
                    ref={selectCustomRef}
                    disabled={assetType === undefined}
                    classNamePrefix="select"
                    isClearable={true}
                    name="color"
                    options={colourOptions2}
                    placeholder="Select Field Type..."
                    onChange={(e) => this.setState({ ftype: e ? e.value : '' })}
                  />
                  <Label for="nameMulti">Field Type</Label>
                </FormGroup>
              </ModalBody>
              <ModalFooter>
                <FormGroup
                  className="form-label-group"
                  style={{ textAlign: 'right' }}
                >
                  <Button.Ripple
                    outline
                    color="secondary"
                    type="reset"
                    className="mb-1 button-label"
                    onClick={(e) => {
                      this.clearCustomField()
                    }}
                  >
                    {messages?.resetButton ? messages?.resetButton : 'Reset'}
                  </Button.Ripple>
                  <Button.Ripple
                    color="warning"
                    disabled={fname === '' || ftype === ''}
                    //disabled={assetType === undefined}
                    type="reset"
                    className="button-label"
                    onClick={(e) => this.addfield()}
                  >
                    {messages?.submitButton ? messages?.submitButton : 'Reset'}
                  </Button.Ripple>
                </FormGroup>
              </ModalFooter>
            </Modal>
          </Col>
        </Row>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    dataList: state.dataList,
    listLoading: state.dataList.totalRecordsLoading
  }
}

export default connect(mapStateToProps, { getData, addData })(AddAssets)

